.header {
  width: 1200px;
  margin: auto;

  .search-form {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .search-flex {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;

      .logo-icon {
        cursor: pointer;
      }

      .search-bar {
        height: 3rem;
        width: 20rem;
        background: white;
        border: 1px solid #cfd4d8;
        border-radius: 2px;
        padding: 0.5rem;
      }

      .search-button {
        justify-content: center;
        align-items: center;
        border: none;
        background-color: white;
        height: 40px;
        cursor: pointer;
        transform: translate(-40px, 0px);
      }
    }

    .feedback-button {
      display: flex;
      height: 48px;
      justify-content: center;
      align-items: center;
    }
  }
}

.search-results {
  position: relative;
  width: 1200px;
  margin: auto;

  .term-definition {
    text-align: left;
    text-transform: capitalize;
    margin: 2rem 0;
    font-size: larger;

    .term {
      font-weight: bold;
    }

    .definition {
      display: none;
    }
  }
}
