.logo-spinner {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 50%;
  left: 50%;
  /* bring your own prefixes */
  transform: translate(-50%, -50%);

  .logo {
    cursor: pointer;
  }

  /**
 * ==============================================
 * Dot Pulse
 * ==============================================
 */
  .dot-pulse {
    position: relative;
    margin-left: 20px;
    left: -9999px;
    width: 10px;
    height: 10px;
    border-radius: 5px;
    background-color: #1b47ff;
    color: #1b47ff;
    box-shadow: 9999px 0 0 -5px #1b47ff;
    animation: dotPulse 1.5s infinite linear;
    animation-delay: 0.25s;
  }

  .dot-pulse::before,
  .dot-pulse::after {
    content: "";
    display: inline-block;
    position: absolute;
    top: 0;
    width: 10px;
    height: 10px;
    border-radius: 5px;
    background-color: #1b47ff;
    color: #1b47ff;
  }

  .dot-pulse::before {
    box-shadow: 9984px 0 0 -5px #1b47ff;
    animation: dotPulseBefore 1.5s infinite linear;
    animation-delay: 0s;
  }

  .dot-pulse::after {
    box-shadow: 10014px 0 0 -5px #1b47ff;
    animation: dotPulseAfter 1.5s infinite linear;
    animation-delay: 0.5s;
  }

  @keyframes dotPulseBefore {
    0% {
      box-shadow: 9984px 0 0 -5px #1b47ff;
    }
    30% {
      box-shadow: 9984px 0 0 2px #1b47ff;
    }
    60%,
    100% {
      box-shadow: 9984px 0 0 -5px #1b47ff;
    }
  }

  @keyframes dotPulse {
    0% {
      box-shadow: 9999px 0 0 -5px #1b47ff;
    }
    30% {
      box-shadow: 9999px 0 0 2px #1b47ff;
    }
    60%,
    100% {
      box-shadow: 9999px 0 0 -5px #1b47ff;
    }
  }

  @keyframes dotPulseAfter {
    0% {
      box-shadow: 10014px 0 0 -5px #1b47ff;
    }
    30% {
      box-shadow: 10014px 0 0 2px #1b47ff;
    }
    60%,
    100% {
      box-shadow: 10014px 0 0 -5px #1b47ff;
    }
  }
}
