.search-result-entry {
  position: relative;
  display: flex;
  width: 100%;
  height: max-content;
  margin: 2rem 0px;
  border: 1px solid #cfd4d8;
  justify-content: flex-start;
  background-color: white;
  color: #070707;

  img,
  iframe {
    cursor: pointer;
    position: relative;
    text-align: center;
    border-bottom: none;
  }

  .video-player {
    display: flex;
    flex-direction: column;
    position: relative;

    .video-player-footer {
      background-color: #f5f5f5;
      width: 100%;
      height: 33px;
      position: relative;
      border-top: 1px solid #cfd4d8;
    }
  }

  .entry-details {
    padding-left: 1rem;
    padding-right: 1rem;
    text-align: left;
    width: min-content;
    height: auto;
    position: relative;
    flex-grow: 2;
    background-color: white;

    .video-name {
      font-weight: bold;
      font-size: large;
      display: block;
    }
    .video-date {
      font-size: small;
      display: block;
      color: #686868;
    }

    .first-context {
      margin-top: 10px;
      font-size: medium;
      height: 100px;
      overflow: hidden;
    }

    .view-transcript-button {
      display: block;
      cursor: pointer;
      position: relative;
      border: none;
      color: #0885de;
      text-align: left;
      padding-left: 0;
      background-color: white;
      font-weight: bold;
    }

    .view-transcript-button:hover {
      font-weight: bold;
    }

    .nav-buttons {
      display: flex;
      flex-direction: row;
      padding-top: 10px;

      button {
        width: 100px;
        text-align: center;
        padding: 10px 5px;
        border: 1px solid #cfd4d8;
        border-radius: 2px;
        cursor: pointer;
        background-color: #f5f5f5;
      }

      .selected-button {
        background-color: #0a2747;
        color: white;
        border: 1px solid #0a2747;
      }
    }

    .full-transcript,
    .time-context-entries {
      position: relative;
      margin-top: 10px;
      font-size: medium;
      overflow: auto;
      width: 100%;
      height: 280px;
      line-height: 1.3;
    }

    .time-context-item {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: flex-start;
      cursor: pointer;
      padding-bottom: 10px;
      border-radius: 2px;

      .time {
        width: 15%;
        font-weight: bold;
      }

      .context {
        width: 85%;
        border-radius: 2px;
        padding-left: 5px;
      }
    }

    .time-context-item:hover {
      .time {
        color: #0885de;
      }
      .context {
        background-color: #f5f5f5;
      }
    }

    .entry-details-fotter {
      background-color: #f5f5f5;
      width: 100%;
      height: 33px;
      position: absolute;
      bottom: 0px;
      right: 0px;
      border-top: 1px solid #cfd4d8;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      flex-direction: row;

      button {
        margin: 2px;
        cursor: pointer;
        color: #303b44;
        background-color: #f5f5f5;
        border: 1px solid #cfd4d8;
        border-radius: 2px;
        padding: 2px 5px;
      }

      .new-tab-button {
        display: none;
      }

      .expand-shrink-button {
        border: none;
        align-self: center;
      }

      .arrow {
        border: none;
      }

      .up {
        transform: rotate(180deg);
        -webkit-transform: rotate(180deg);
      }
    }
  }
}
